body {
  box-sizing: content-box !important; }

*,
*:before,
*:after {
  box-sizing: inherit !important; }

.f-fieldset--outlined .f-fieldset__indicator--optional {
  line-height: 2; }

.f-fieldset--outlined .f-fieldset__indicator--required {
  line-height: 1; }

.f-fieldset--outlined .f-fieldset__label {
  position: absolute;
  z-index: 1;
  display: flex;
  align-items: center;
  line-height: 1;
  top: 0;
  left: 1em;
  transform: translateY(-50%); }

.f-fieldset--outlined .f-fieldset__label-text:before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: -0.35em;
  right: -0.35em;
  z-index: -1;
  background-color: white; }

.f-fieldset--outlined .f-fieldset__inner {
  border: 1px solid silver;
  border-radius: 0.25em;
  padding-top: 1.5em;
  padding-bottom: 1.5em;
  padding-left: 1em;
  padding-right: 1em; }

.f-fieldset--outlined.has-error .f-fieldset__inner {
  border-color: #e91e63;
  box-shadow: inset 0 0 0 calc(2px - 1px) #e91e63; }

.f-fieldset--outlined.has-error .f-fieldset__label .f-fieldset__label-text {
  color: #e91e63; }
